<script>
import Vue from "vue";
import { GridPlugin, Page, Sort, Group, Resize, Toolbar, Search, CommandColumn, ExcelExport, PdfExport } from "@syncfusion/ej2-vue-grids";
import api from "@/services/http";
Vue.use(GridPlugin);

export default {
  data() {
    return {
      date: new Date().toISOString().substr(0, 10),
      MenuTglApprove: false,
      HakCuti:null,
      DialogLihatCuti:false,
      DataLihatCuti:[],
      JumlahCuti:null,
      DataNotif:[],
      PilihanOpsi:null,
      Select:[{no:1,text:'Opr Div Head',value:1}],
      mobile:null,
      valid:null,
      DefaultDataApproval:{
          karyawan:{
            Nama:'',
            KodeKaryawan:''
          },
          cuti:{
            id: '',
            TanggalPengajuan: '',
            TanggalMulai: '',
            JamMulai: '',
            TanggalSelesai: '',
            JamSelesai: '',
            JenisCuti: '',
            AlasanCuti: '',
            StatusCuti: ''
          },
      },
      DataApproval:{
          karyawan:{
            Nama:'',
            KodeKaryawan:''
          },
          cuti:{
            id: '',
            TanggalPengajuan: '',
            TanggalMulai: '',
            JamMulai: '',
            TanggalSelesai: '',
            JamSelesai: '',
            JenisCuti: '',
            AlasanCuti: '',
            StatusCuti: ''
          },
      },
      Permission:[],
      DialogApprove:false,
      ValidCreate:false,
      passbaru:null,
      textAktif:true,
      umenu:[],
      verifpassbaru:null,
      loading:true,
      Kode:null,
      DialogPilihExport:false,
      windowSize: {x: 0, y: 0},
      MenuExport: false,
      commands: [],
      customAttributes : {class: 'customcss'},
      Alert:false,
      akun:[],
      usermenu:null,
      AlertDataTerhapus:false,
      AlertBerhasilTerubah:false,
      user:JSON.parse(localStorage.getItem('user')),
      token:localStorage.getItem('token'),
      DialogTambahApprovalCuti: false,
      form: new FormData,
      editedIndex: -1,

      defaultItem: {
        id:"",
        NoCuti: "",
        ApproveBy: "",
        JenisApproval: "",
        TanggalApprove: "",
        StatusApproval: "",
        Keterangan: "",
      },
      editedItem: {
        id:"",
        NoCuti: "",
        ApproveBy: "",
        JenisApproval: "",
        TanggalApprove: "",
        StatusApproval: "",
        Keterangan: "",
      },
      toolbarOptions: ['Search'],
      DataApprovalCuti: [],
      pageSettings: {pageSize: 10, pageSizes :['5','10','15','20','50','All']},
    };
  },
  

  provide: {
    grid: [Page, Sort, Group, Resize, Toolbar, CommandColumn, Search,ExcelExport,PdfExport ]
  },

 async mounted(){
    Vue.prototype.$BukaNotifPengajuanCuti = this.BukaNotifPengajuanCuti
    if (this.Permission.some(a => a == 'R')) {
      this.commands.push({buttonOption: { cssClass: "e-flat Lihat", iconCss: "e-icons mata", content:' '} })
      this.valid = true
      this.getdata()
    }else{
      this.loading = false
      this.valid = false
    }
    if (this.Permission.some(a => a == 'C')) {
     this.ValidCreate = true
    }
    if (this.Permission.some(a => a == 'U')) {
     this.commands.push({buttonOption: { cssClass: "e-flat Edit", iconCss: "e-edit e-icons", content:' '} })
    }
    if (this.Permission.some(a => a == 'D')) {
     this.commands.push({ buttonOption: { cssClass:"e-flat Delete", iconCss: "e-delete e-icons", content:' '} })
    }
  },
  
  created(){
    //get action for this page
    let permisi = this.$route.params.Permission
    this.Permission = permisi == null ? [] : permisi.split(',')
    },

  computed: {
    formTitleApprovalCuti() {
      return this.editedIndex === -1 ? 'Tambah Data Approval Cuti' : 'Ubah Data Approval Cuti'
    },
    Status () {
      return this.editedIndex === -1 ? 'Baru' : 'Ubah'
    },
  },
  
  watch: {
    PilihanOpsi(){
      console.log('ceked',this.PilihanOpsi)
    },
      // let a = this.DataAppovalCuti.filter( function(item){return (item.UserMenu == newval.toUpperCase());} )
      // this.umenu = a
    windowSize(){
      if (this.windowSize.x < 450) {
        this.titleClass = "d-none"
        this.mobile = true
      }else{
        this.titleClass = "mr-4"
        this.mobile = false
      }
    },

    DialogTambahApprovalCuti (val) {
      val || this.KeluarDialogApprovalCuti()
    },

    Alert(){
      this.AlertItem()
    },
    AlertDataTerhapus(){
      this.AlertItem()
    },
    AlertBerhasilTerubah(){
      this.AlertItem()
    },
  },

  methods: {
    onResize(){
      this.windowSize = { x: window.innerWidth, y: window.innerHeight }
    },
    ApproveCutiBtn(){
      this.Toast('Approval Di Setujui')
      let notiftujuan = this.$user.jabatan.Atasan.id
      if (this.PilihanOpsi != null) {
        notiftujuan = this.PilihanOpsi
      }
      api.put("/approvecuti/"+this.DataApproval.id+'/?token='+this.token,{
            NoCuti: this.DataApproval.NoCuti,
            ApproveBy: this.$user.Nama,
            JenisApproval: this.DataApproval.JenisApproval,
            TanggalApprove: new Date().toJSON().slice(0,10),
            StatusApproval: "Approved",
            Keterangan: this.DataApproval.Keterangan,
            Dari:this.$user.KodeKaryawan,
            HakCuti:this.HakCuti,
            JumlahCuti:this.JumlahCuti,
            TujuanNotif:notiftujuan,
            Konten:this.DataApproval.Keterangan,
            Opsi:this.PilihanOpsi,
					},
          { headers: {"Authorization" : `Bearer ${this.token}` } })
					.then((res) => {
						if (!res) {
							//do nothing
						}else{
              if(this.$route.params.notifikasi != undefined){
                  delete this.$route.params.notifikasi
                }
              this.getdata()
              this.AlertBerhasilTerubah = true
						}
						
					})
      this.DialogApprove = false
    },
    RejectCutiBtn(){
      this.Toast('Approval Di Reject')
      let TujuanNotif = [this.DataApproval.karyawan.KodeKaryawan]
      if (this.DataApproval.Level == 2) {
        TujuanNotif = [this.$route.params.notifikasi.karyawan.KodeKaryawan,this.DataApproval.karyawan.KodeKaryawan]
      }
      if (this.DataApproval.Level == 3) {
        TujuanNotif = [this.$route.params.notifikasi.karyawan.KodeKaryawan,this.DataApproval.karyawan.KodeKaryawan]
      }
      api.put("/rejectcuti/"+this.DataApproval.id+'/?token='+this.token,{
            NoCuti: this.DataApproval.NoCuti,
            ApproveBy: this.$user.Nama,
            JenisApproval: this.DataApproval.JenisApproval,
            TanggalApprove: new Date().toJSON().slice(0,10),
            Keterangan: this.DataApproval.Keterangan,
            Dari:this.$user.KodeKaryawan,
            StatusApproval: "Rejected",
            // Tujuan:this.DataApproval.karyawan.KodeKaryawan,
            TujuanNotif:TujuanNotif,
            Konten:this.DataApproval.Keterangan
					},
          { headers: {"Authorization" : `Bearer ${this.token}` } })
					.then((res) => {
						if (!res) {
							//do nothing
						}else{
              if(this.$route.params.notifikasi != undefined){
                  delete this.$route.params.notifikasi
                }
              this.getdata()
              this.AlertBerhasilTerubah = true
						}
						
					})
      this.DialogApprove = false
    },

    BukaNotifPengajuanCuti(data){
      this.DialogApprove = true
      this.DataNotif = data
      let approv = this.DataApprovalCuti.find(x => x.NoCuti == data.KodeNotifikasi)
      this.DataApproval = approv.length > 0 ? approv[0] : []
      this.HakCuti = this.DataApproval.karyawan.Cuti
    },

    cariKode(Kode){
      if (Kode != null) {
          this.textAktif = false
        }else{
          this.textAktif = true
        }
      if (Kode == null || Kode == '' || Kode.length == 0) {
        this.editedIndex = -1
          this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      }else{
      this.Kode = Kode.toUpperCase()
      let a = this.DataApprovalCuti.filter( function(item){return (item.Kode == Kode.toUpperCase());} );
      if (a.length > 0) {
          this.editedIndex = 1;
          this.textAktif = false
          this.editedItem.Nama = a[0].Nama
        }
      }
    },
  async saya(){
       if (this.$menu == undefined) {
         await api.get('/menu?token='+this.token,{ headers: {"Authorization" : `Bearer ${this.token}`} }).then(
        res=>{
          if (res) {
            this.akun = res.data
            let route = this.$route.name
            let a = this.akun.filter( function(item){return (item.Route == route);} );
            if (a.length > 0) {
               this.valid = true
            }else{
              this.valid = false
              this.loading = false
            }
            //do noting
          }
        }).catch(err=>{
          // alert(err)
          if (err == "Error: Request failed with status code 401" && this.$route.path != "/login") {
            this.logout()
          }
        })
       }else{
        this.akun = this.$menu
        let route = this.$route.name
            let a = this.akun.filter( function(item){return (item.Route == route);} );
            if (a.length > 0) {
               this.valid = true
            }else{
              this.valid = false
              this.loading = false
            }
       }     
    },
      // cekmenu(){
        
      // },

    Simpan(){
      if(this.formTitleApprovalCuti === "Tambah Data Approval Cuti"){
        this.Toast('Data Disimpan')
          api.post('/approvalcuti?token='+this.token,{
            NoCuti: this.editedItem.NoCuti,
            ApproveBy: this.editedItem.ApproveBy,
            JenisApproval: this.editedItem.JenisApproval,
            TanggalApprove: this.editedItem.TanggalApprove,
            StatusApproval: this.editedItem.StatusApproval,
            Keterangan: this.editedItem.Keterangan,
            },{ headers: { 'Content-Type': 'multipart/form-data',"Authorization" : `Bearer ${this.token}` } })
            .then((res)=>{
              if (res) {
                this.Alert = true
                this.getdata()
              } else {
                //
              }
            })
      }else if(this.formTitleApprovalCuti === "Ubah Data Approval Cuti"){
        this.Toast('Data Disimpan')
         api.put("/approvalcuti/"+this.editedItem.id+'/?token='+this.token,{
            NoCuti: this.editedItem.NoCuti,
            ApproveBy: this.editedItem.ApproveBy,
            JenisApproval: this.editedItem.JenisApproval,
            TanggalApprove: this.editedItem.TanggalApprove,
            StatusApproval: this.editedItem.StatusApproval,
            Keterangan: this.editedItem.Keterangan,
					},
          { headers: {"Authorization" : `Bearer ${this.token}` } })
					.then((res) => {
						if (!res) {
							//do nothing
						}else{
              this.getdata()
              // this.AlertBerhasilTerubah = true
						}
						
					})
      }
      this.KeluarDialogApprovalCuti()    
    },

    commandClick: function(args) {
      // if (args.target.classList.contains("custombutton")){
      // }else 
      if (args.target.classList.contains("Lihat")) {
        this.DialogLihatCuti = true
        let a = this.DataApprovalCuti.filter( function(item){return (item.NoCuti == args.rowData.NoCuti);} );
        this.DataLihatCuti = a[0]
        console.log(a)
      }else if (args.target.classList.contains("Delete")){
        var r = confirm("Yakin Hapus Data?");
        if (r == true) { 
          this.Toast('Data Dihapus')
          api
            .delete("/approvalcuti/"+args.rowData.id+'?token='+this.token)
            .then((res) =>{
              // console.log(res);
              if (res) {
                this.getdata()
              // this.AlertDataTerhapus = true
              }
              
            })
            .catch((err) => {
              console.log(err);
            });
        }
      } else if (args.target.classList.contains("Edit")){
        this.NoCuti = args.rowData.NoCuti
        let data = args
            this.editedIndex = 1;
            this.editedItem = data.rowData
            this.DialogTambahApprovalCuti = true
      }
    },

    PdfExport(){
      let data = document.getElementById('approvalcutiview').ej2_instances[0];
      // console.log(data)
      data.pdfExportProperties = {
        pageOrientation: 'Landscape',
        fileName:"ReportApprovalCuti.pdf"
      }
      data.columns[1].visible = false
      data.columns[4].visible = false
      data.columns[3].visible = false
      // let pdfExportProperties = {
       
      // };
      // this.$refs.gridappovalcuti.columns[0].visible = false;
      // this.$refs.gridappovalcuti.columns[1].visible = true;
      this.$refs.gridapprovalcuti.pdfExport(data.pdfExportProperties);
    },
    excelExport(){
      this.$refs.gridapprovalcuti.excelExport();
      // console.log(this.$refs.gridappovalcuti)
      // let data = document.getElementById('appovalcutiview').ej2_instances[0];
      // console.log(data)
      // data.excelExport();
    },

    KeluarDialogApprovalCuti () {
      this.DialogTambahApprovalCuti = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
        this.Kode = null
        this.textAktif = true
      })
    },

    // beforeExport: function(args) {
      
    // },

    getdata(){
      api.get('/approvalcuti?token='+this.token,
      // { headers: {"Authorization" : `Bearer ${this.token}`} }
      ).then(
				res => {
          if (res) {
            let datas = [], approval = [], approvalcuti = []
            let es = res.data
            var flags = [], l = res.data.length, i;
            for( i=0; i<l; i++) {
                if( flags[res.data[i].NoCuti]) continue;
                flags[res.data[i].NoCuti] = true;
                datas = es.filter(w=> w.NoCuti == res.data[i].NoCuti)
                datas.NoCuti = datas[datas.length-1].NoCuti
                datas.ApproveBy = datas[datas.length-1].ApproveBy
                datas.TanggalApprove = datas[datas.length-1].TanggalApprove
                datas.JenisApproval = datas[datas.length-1].JenisApproval
                datas.StatusApproval = datas[datas.length-1].StatusApproval
                datas.Keterangan = datas[datas.length-1].Keterangan
                datas.DiBuatOleh = datas[datas.length-1].DiBuatOleh
                datas.DiBuatTgl = datas[datas.length-1].DiBuatTgl
                datas.DiUbahOleh = datas[datas.length-1].DiUbahOleh
                datas.DiUbahTgl = datas[datas.length-1].DiUbahTgl
                let s = datas
                let sa = datas.reverse()
                approval.push(s)
                approvalcuti.push(sa)
            }
            console.log('approval',approval)
            console.log('dirp',approvalcuti)
            // this.DataApprovalCuti = res.data
            // console.log('datacuti',res.data)
            // cek notif cuti
            let data = res.data
            data = data.reverse()
            if(this.$route.params.notifikasi != undefined){
            let notifcuti = this.$route.params.notifikasi
              if (notifcuti.Judul == 'Pengajuan Cuti') {
                this.DataNotif = notifcuti
                this.DialogApprove = true
                console.log(notifcuti)
                let approv = data.find(x => x.NoCuti == notifcuti.KodeNotifikasi)
                this.DataApproval = approv
                this.HakCuti = this.DataApproval.karyawan.Cuti
                 console.log('hasil',approv)
              }           
            }
          this.DataApprovalCuti = approvalcuti
          this.loading = false
          }
          
				},
				err => {
					console.log(err);
				}
			)
    },

    Reset() {
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
      })
    },

    Toast(data){
      this.$swal.fire({
      toast: true,
      icon: 'success',
      title: data,
      animation: false,
      position: 'top-right',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true})
    },

    logout(){
        api.post('/logout', this.token)
        .then(res=>{
          // console.log(res)
          if (res) {
            //do nothing
          }
        })
        localStorage.removeItem('token')
        localStorage.removeItem('user')
        location.reload(false)
      },

    AlertItem(){
      window.setInterval(() => {
        this.Alert = false;
        this.AlertDataTerhapus = false;
        this.AlertBerhasilTerubah = false;
      }, 3000)
    },
  },
}
</script>

